@import "https://fonts.googleapis.com/css2?family=DM+Sans:wght@700&family=Titan+One&display=swap";
:root {
  --products-title-font-family: "Titan One", sans-serif;
  --products-text-font-family: "DM Sans", sans-serif;
  --container-bg-color: #fff;
  --h1-title-color: #d41443;
  --h2-products-color: #fff;
  --text-color: #1e1c1c;
  --products-card-bg-color: #ffb8ca;
  --item-modified-color: #fff;
  --item-text-color: #fff;
  --container-label-color: #ffa5ba;
  --button-bg-color: #ffb8ca;
  --modal-content-bg: #a5a2db;
  --modal-form-text-color: #212121;
  --transition: 250ms cubic-bezier(.4, 0, .2, 1);
}

.container {
  max-width: 1200px;
  background-color: var(--container-bg-color);
  margin: 0 auto;
  padding-top: 61px;
  padding-bottom: 62px;
}

.container__label {
  font-family: var(--products-title-font-family);
  text-align: center;
  letter-spacing: .04em;
  text-transform: uppercase;
  color: var(--container-label-color);
  padding-top: 61px;
  font-size: 30px;
}

body {
  font-family: var(--products-text-font-family);
  color: var(--text-color);
  font-style: normal;
  font-weight: 400;
  line-height: 1.13;
}

h1 {
  font-family: var(--products-title-font-family);
  text-align: center;
  letter-spacing: .06em;
  text-transform: uppercase;
  color: var(--h1-title-color);
  padding-top: 16px;
  padding-bottom: 20px;
  font-size: 58px;
}

h2 {
  font-family: var(--products-title-font-family);
  text-align: center;
  letter-spacing: .04em;
  text-transform: uppercase;
  color: var(--h2-products-color);
  font-size: 30px;
}

.products {
  flex-direction: row;
  justify-content: space-evenly;
  gap: 20px;
  margin: 0 28px;
  display: flex;
}

.products__card-set {
  background: var(--products-card-bg-color);
  width: 368px;
  border-radius: 24px;
  flex-direction: column;
  align-items: center;
  padding: 165px 32px 60px;
  list-style-type: none;
  display: flex;
}

.products__pic {
  width: 368px;
  background-color: rgba(0, 0, 0, 0);
  position: relative;
  top: 120px;
  left: 0;
}

.products__item-text {
  font-family: var(--products-text-font-family);
  text-align: center;
  letter-spacing: .04em;
  color: var(--item-text-color);
  padding-bottom: 32px;
  font-size: 16px;
  font-weight: 700;
  line-height: 176%;
}

.products__item--modified:after {
  content: "";
  background: var(--item-modified-color);
  height: 6px;
  width: 6px;
  vertical-align: middle;
  border-radius: 50%;
  margin: 32px 0;
  display: inline-block;
}

.arrow {
  background-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
}

.modal {
  width: 100vw;
  height: 100vh;
  transition: transform var(--transition);
  justify-content: center;
  align-items: center;
  padding: 40px;
  display: flex;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.modal__content {
  background: var(--modal-content-bg);
  width: 30%;
  height: auto;
  border-radius: 15px;
  margin: 0 auto;
  padding: 30px;
  position: relative;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.modal__form-text {
  text-align: center;
  letter-spacing: .03em;
  color: var(--modal-form-text-color);
  padding: 40px;
  font-size: 20px;
  line-height: 1.15;
}

.is-hidden {
  visibility: hidden;
  transform: scale(1.1);
}

.button {
  background-color: var(--button-bg-color);
  cursor: pointer;
  border: none;
}

.close-btn {
  cursor: pointer;
  color: #000;
  background: #a5a2db;
  border: none;
  outline: none;
  position: absolute;
  top: 8px;
  right: 8px;
}

.close-btn:hover, .close-btn:focus {
  transition: transform var(--transition);
  background: #dedcdc;
}

